@import url(https://fonts.googleapis.com/css?family=Signika:700,300,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.AllIconsDisplay {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.navContainer,
.Jokedisplay,
.dictionarydisplay,
.AdviceDisplay,
.QuotesDisplay,
.NewsDisplay,
.musicDisplay,
.EventsTable {
  margin-top: 2em;
  border-radius: 1em;
  border: groove;
  border-color: black;
  padding: 1em;
  margin-right: 5%;
  margin-left: 5%;
  box-shadow: 1px 2px 20px 7px #888;
  opacity: 0.85;
}
.dictionaryWord {
  color: black;
  font-size: 3em;
  font-weight: bolder;
  padding-left: 2%;
}
.dictionaryDefinition {
  color: rgb(43, 42, 42);
  font-size: 2em;
  padding-left: 2%;
}
.dictionaryPronunciation {
  color: rgb(85, 79, 79);
  padding-left: 2%;
  padding-top: 1%;
}

.advice,
.Quote,
.Jokedisplay {
  font-size: 2em;
  font: bold "Signika", sans-serif;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.advice span,
.Quote div {
  display: inline-block;
  animation: bounce 0.6s;
}
@keyframes float {
  0%,
  100% {
    transform: none;
  }
  33% {
    transform: translateY(-1px) rotate(-2deg);
  }
  66% {
    transform: translateY(1px) rotate(2deg);
  }
}
.advice:hover span {
  animation: float 0.2s ease-in-out infinite;
}
@keyframes bounce {
  0%,
  100% {
    transform: translate(0);
  }
  25% {
    transform: rotateX(20deg) translateY(2px) rotate(-3deg);
  }
  50% {
    transform: translateY(-20px) rotate(3deg) scale(1.1);
  }
}

.Newstitle {
  font-size: x-large;
}
.Newsdescription {
  font-size: small;
}

.GIFimageDisplay {
  margin-top: 2em;
  border-radius: 1em;
  border: groove;
  border-color: rgb(4, 131, 131);
  padding: 1em;
  margin-left: 40%;
  box-shadow: 1px 2px 20px 7px #888;
  opacity: 0.85;
}
table {
  font-size: 16px;
  font-family: sans-serif;
  border-collapse: collapse;
  border: 1px solid rgba(15, 14, 14, 0.867);
  width: 40%;
  overflow-x: auto;
}

thead tr {
  background-color: teal;
  color: white;
  text-align: center;
}
thead th {
  padding: 12px 15px;
}
tr,
td {
  padding: 12px 15px;
  border-bottom: 1px solid rgba(185, 179, 179, 0.867);
}
tbody tr:hover {
  background-color: rgba(0, 128, 128, 0.1);
}
.pieChart,
.EventsTable {
  margin-top: 5%;
  margin-left: 30%;
}

.displayEachIconContainer:hover {
  transform: scale(1.5);
}
.displayEachIconContainer {
  align-items: center;
  padding-top: 1%;
}
.quoteauthorname {
  color: gray;
  display: flex;
  justify-content: flex-end;
  font-size: larger;
}

q::before,
q::after {
  animation: dancing 1s linear infinite;
  display: inline-block;
  transform: rotate(13deg);
}

@keyframes dancing {
  0% {
    transform: rotate(13deg) scale(2);
  }
  25% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(-13deg) scale(2);
  }
  75% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(13deg) scale(2);
  }
}
video {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}

.slider {
  padding-top: 2%;
  padding-left: 10%;
  padding-right: 10%;
  padding-bottom: 2%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40vh;
  overflow-y: hidden;
}

.image {
  width: 100%;
}

.right-arrow {
  position: absolute;
  top: 50%;
  right: 32px;
  font-size: 3rem;
  color: black;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.left-arrow {
  position: absolute;
  top: 50%;
  left: 32px;
  font-size: 3rem;
  color: black;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.08);
}



/* Player */
.no-height {
    height: 0 !important;
  }
  .player-container {
    margin-top: 2em;
    width: 435px;
    margin: 0 auto;
    overflow: hidden;
    background-color: rgb(238, 236, 236);
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  }
  .player-cover {
    height: 160px;
    background-position: top center;
    background-size: cover;
    transition: height 0.4s;
  }
  .artist-info {
    padding: 60px 35px 35px;
  }
  .artist-name,
  .artist-song-name {
    margin: 0;
  }
  .artist-name {
    margin-bottom: 5px;
    font-size: 32px;
    font-weight: 400;
  }
  .artist-song-name {
    font-size: 18px;
    font-weight: 400;
  }
  .player-progress-container {
    position: relative;
    width: 365px;
    height: 9px;
    margin: 0 auto;
    background-color: #414141;
    border-radius: 40px;
  }
  .player-progress-value {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    background-color: #cdd2d8;
    border-radius: 40px;
  }
  .player-progress-value:after {
    position: absolute;
    top: -3px;
    right: -6px;
    content: "";
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: black;
    box-shadow: rgba(0, 0, 0, 0.2) 0 0 5px 2px;
  }
  .player-options {
    display: flex;
    justify-content: space-between;
    padding: 40px 0 20px;
    width: 365px;
    margin: 0 auto;
  }
  .player-buttons {
    display: inline-flex;
    justify-content: space-between;
    align-items: center;
    width: 135px;
  }
  .player-btn {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: #d2d7db;
    background-color: transparent;
    border: 0;
    border-radius: 50%;
    outline: 0;
    transition: all 0.4s;
  }
  .player-btn i {
    font-size: 28px;
    margin: 0 auto;
  }
  .player-btn.active {
    color: #838c92;
  }
  .player-btn.big {
    width: 70px;
    height: 70px;
  }
  .player-btn.big i {
    font-size: 36px;
  }
  .player-btn.medium {
    width: 50px;
    height: 50px;
  }
  .player-btn.medium i {
    font-size: 15px;
  }
  .player-btn.volume {
    width: 40px;
  }
  .player-btn:disabled {
    opacity: 0.5;
  }
  .player-controls {
    width: 180px;
  }
  .player-controls .player-btn {
    color: #d9d9d9;
    background-color: #414141;
  }
